      "SIGNCHECK的全貌在2018年正式问世，但它的胚胎在1995年已开始孕育，甚至更早⋯⋯\n\n彼时，在国际享有盛誉的头部设计公司HBA里，有Victor先生的一席之地。 凭借着过硬的设计知识储备与长达25年的全球项目经验，他摘获荣誉连连，也沉淀着国际视野和业务技能。",
