      "2018年，基于公司业务倾向的改变，和想要专心做标识设计的心愿，积攒了足够多项目经验的Christy和Max决定创办SIGNCHECK拾创，在保持传统专业优势的基础上，凭借年轻冲劲与创新意识，为香港、大陆持续带去标识的力量。\n\n她们来到大陆，创办位于深圳的工作室落座在充满活力和自由气息的深圳，艰辛但令人兴奋的创业之旅开始了。就这样，仰赖着对标识的信仰和执念，怀抱着「让标识拥有活力和灵魂，建立建筑物与用户之间的美妙联结」的心愿，SIGNCHECK诞生并正式进军内地市场。 ",
