      "漫長的工作生涯裏，Victor對飯店、商場等公共建築物的瞭解愈發深厚，他深知標識設計對建築物至關重要，同時，他窺得標識設計在大陸地區與香港近乎是「缺位」狀態，毅然決定到香港進行標識設計市場的開拓。\n\n隨著洛杉磯到香港的航線落下，港島第三家標識設計公司正式誕生。 在當地，Victor即使談不上是標識設計的開創者，也算是半個奠基者。\n\n有著敏銳的市場嗅覺和過硬的設計能力，很快，公司在Victor領導下飛速發展，不斷精耕環境製圖、列印圖形、均勻設計和案頭造型，團隊憑藉出色的專業能力拿下超50個五星級高端飯店項目，在國際上的影響力也日益擴大，更在2000年一躍成為亞洲最優秀的標識設計公司。",
      "發展到第15個年頭時，公司在國際範圍內已是「炙手可熱」，項目以豪華酒店為主，落地在五大洲......",
      "業務不斷擴張，當然也需要更多才華橫溢的夥伴加入。2012年，Christy和Max進入Victor團隊，以專業設計和技術總監的身份，為團隊注入更加年輕化的能量、謀求更大的可能性\n\n二人行事切實且執著，有著「不服輸」和探險精神的她們，曾孤身離家去往外大城市國家，只為更好地進行設計工作，接觸不同地區、不同風格的設計理念。",
      "畢業於香港理工大學-美術學位專業的Christy，除了社會經驗外，還有不少課本知識的基礎，使她不失國際視野、創新精神和良好的審美，加上幾分較真、些許執著——徬彿天註定她要成為一名優秀進取的設計師。歷經高級學府的研讀和國際知名設計所的鍛鍊，她最終選擇在標識設計這個小眾卻充滿意義的精準領域向下沈澱，全因對著「意義感」有所追求，恰好看到了標識對於建築物的重要性。",
      "2018年，基於公司業務傾向的改變，和想要專心做標識設計的心願，積攢了足夠多項目經驗的Christy和Max決定創辦SIGNCHECK拾創，在保持傳統專業優勢的基礎上，憑藉年輕衝勁與創新意識，為香港、大陸持續帶去標識的力量。\n\n她們來到大陸，創辦位於深圳的工作室落座在充滿活力和自由氣息的深圳，艱辛但令人興奮的創業之旅開始了。就這樣，仰賴著對標識的信仰和執念，懷抱著「讓標識擁有活力和靈魂，建立建築物與用戶之間的美妙聯結」的心願，SIGNCHECK誕生並正式進軍內地市場。",
      "COVID 19的影響遠未散去，市場的競爭更在眼前，創始人的「冒險之旅」卻絲毫沒被動搖。",
      "SIGNCHECK無一刻不在思考未來的腳步如何才能踏得更實，同時也持續探索著更多的可能性。\n\n我們有未來。",
